import * as cnpj from '@fnando/cnpj'
import { FormikProps, useFormik } from 'formik'
import * as Yup from 'yup'

interface InitialValues {
  phone: string
  name: string
  cnpj: string
  email: string
}
const requiredMsg = 'Campo obrigatrio'

export function useRevendedorFormik(): FormikProps<InitialValues> {
  return useFormik<InitialValues>({
    validateOnBlur: false,
    validateOnChange: false,
    validationSchema: Yup.object().shape({
      name: Yup.string().required(requiredMsg),
      cnpj: Yup.string()
        .test('cnpj', 'CNPJ Invlido', (value: any) => {
          console.log(value)
          return cnpj.isValid(value)
        })
        .required(requiredMsg),
      email: Yup.string().email().required(requiredMsg),
      phone: Yup.string().test(
        'is-jimmy',
        'Telefone est fora do padro',
        (value) =>
          stripMaskFromValue(value).length >= 10 &&
          stripMaskFromValue(value).length <= 11
      ),
    }),
    initialValues: {
      cnpj: '',
      name: '',
      email: '',
      phone: '',
    },
    onSubmit: (s) => {
      console.log(s)
    },
  })
}

export function stripMaskFromValue(str?: string): string {
  return (str || '')
    .replaceAll(' ', '')
    .replaceAll('_', '')
    .replaceAll(')', '')
    .replaceAll('(', '')
    .replaceAll('-', '')
}
